import {useState} from 'react';


const Userform = ({submitHandler}) =>{

    
    const [newName, setName] = useState("");
    const [newEmail, setEmail] = useState("");
    const [newAdress, setAdress] = useState("");
    const [newPhone, setPhone] = useState("");

 

    return(

        <form onSubmit={e =>submitHandler(e,newName, newEmail, newAdress, newPhone)}>

            <p>Name:

                <input onChange={e=>setName(e.target.value)}type="text" value={newName}/></p>

            <p>Email :

                <input onChange={e=>setEmail(e.target.value)} type="text" value={newEmail}/></p>

            
            <p> Adress:

                <input onChange={e=>setAdress(e.target.value)} type="text" value={newAdress}/></p>
            
            <p> phone number:

                <input onChange={e=>setPhone(e.target.value)} type="text" value={newPhone}/></p>

            

        </form>

    )

}
export default Userform;