import React, { useState } from 'react';
import { useCart } from 'react-use-cart';
import Summery from './Summery.js';
import Userform from './Userform.js';

const Cart = () => {

  const [userinfo, setUserInfo]= 
    useState({ name: "" ,
      email:"" ,
      address:"" ,
      phonenumber:"" , 
    });

  const [show, setShow] = useState(false);
  const [showSummery, setShowSummery] = useState(false);

  const {
    isEmpty,
    totalUniqueItems,
    items,
    totalItems,
    cartTotal,
    updateItemQuantity,
    removeItem,
    emptyCart,
  } = useCart();

  const submitHandler = (e, name, email, address, phonenumber) => {
    e.preventDefault();
    // handle form submission logic here
  }

  if (isEmpty) return <h1 className="text-center"> Your cart is empty</h1>;

  return (
    <section className="py-4 container">
      <div className="row justify-content-center">
        <div className="col-12">
          <h5> Cart ({totalUniqueItems}) total Items: ({totalItems})</h5>
          <table className="table table-light table-hover m-0">
            <tbody>
              {items.map((item, index) => {
                return (
                  <tr key={index}>
                    <td>
                      <img src={item.img} style={{ height: '6rem' }} alt={item.title} />
                    </td>
                    <td>{item.title}</td>
                    <td>{item.price}</td>
                    <td> Quantity ({item.quantity}) </td>
                    <td>
                      <button className="btn btn-info ms-2"
                        onClick={() => updateItemQuantity(item.id, item.quantity - 1)}
                      >-</button>
                      <button className="btn btn-info ms-2"
                        onClick={() => updateItemQuantity(item.id, item.quantity + 1)}
                      >+</button>
                      <button className="btn btn-danger ms-2"
                        onClick={() => removeItem(item.id)}
                      >remove Item</button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
        <div>
          <div className="col-auto ms-auto">
          <h2> Total price: â‚¬ {cartTotal}</h2>
          </div>
          <div className=" col-auto">
            <button className="btn btn-danger m-2"
              onClick={() => emptyCart()}
            > Clear cart</button>
            <button className="btn btn-danger m-2" onClick={() => setShow(!show)}>
              {show ? "Hide" : "Confirm your payment"} Pay Now
            </button>
            {show && (
              <div>
                <Userform onSubmit={submitHandler} />
                <button className="btn btn-info ms-2" onClick={() => setShowSummery(!showSummery)}>
                  {showSummery ? "Hide Summery" : "Confirm"}
                </button>
                {showSummery && (
                  <div>
                    <Summery />
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Cart;




